/* Tooltip text */
.tooltip::after {
  visibility: hidden;
  font-size: 12px;
  white-space: nowrap;
  background-color: #fff;
  color: #000;
  border: 1px solid #eeeeee;
  text-align: center;
  padding: 5px 10px;
  border-radius: 6px;

  /* Position the tooltip text */
  position: absolute;
  content: attr(data-tooltip);
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
}

/* Tooltip arrow */
.tooltip::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 105%;
  width: 10px;
  height: 10px;
  background: #eee;
  opacity: 0;
  transform: rotate(45deg) translateX(-50%);
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover::after,
.tooltip:hover::before {
  visibility: visible;
  opacity: 1;
}
